import React, { ReactNode } from 'react';
import { InputHint, InputLabel } from 'core/shared';
import { useFormContext } from 'shared/Form/formContext';
import { InputAndFieldCommonProps } from 'core/types/input';
import { InputWrapper, LabelPosition } from 'shared/InputWrapper';
import { ShareableInputProps } from 'shared/types/input';
import { FormSize } from 'shared/Form/types';
import getLabelColor from 'core/utils/getLabelColor';

import wrapperStyles from 'shared/InputWrapper/coreStyles.scss';

export interface FieldWrapperProps extends Pick<ShareableInputProps,
'id'
| 'disabled'
| 'showHint'
| 'isError'
| 'labelAside'
| 'dataTestId'
| 'dataTestHintId'>,
  InputAndFieldCommonProps {
  /** Дочерние элементы */
  children?: ReactNode;

  /** Позиционирование ярлыка при положении сбоку */
  labelAsideSelfAlign?: LabelPosition;
}

function FieldWrapper({
  id,
  size = 'default',
  label,
  labelAside,
  labelAsideSelfAlign,
  labelWidth,
  disabled,
  isError,
  dataTestHintId,
  hintMessage,
  showHint,
  dataTestId,
  children
}: FieldWrapperProps): JSX.Element {
  const { SIZE, LABEL_ASIDE, LABEL_WIDTH } = useFormContext({
    size: size as FormSize,
    labelAside,
    labelWidth
  });

  const renderedLabel = label && (
    <InputLabel
      color={ getLabelColor(disabled) }
      size={ SIZE }
      text={ label }
      htmlFor={ id }
    />
  );

  const renderedHint = showHint && (
    <InputHint
      disabled={ disabled }
      isError={ isError }
      dataTestId={ dataTestHintId }
      hintMessage={ hintMessage }
    />
  );

  return (
    <InputWrapper
      label={ renderedLabel }
      labelAside={ LABEL_ASIDE }
      labelAsideSelfAlign={ labelAsideSelfAlign }
      isError={ isError }
      showHint={ showHint }
      hint={ renderedHint }
      size={ SIZE }
      disabled={ disabled }
      styles={ wrapperStyles }
      labelWidth={ LABEL_WIDTH }
      dataTestId={ dataTestId }
    >
      { children }
    </InputWrapper>
  );
}

export { FieldWrapper };
